import React, { useState } from 'react';
import { 
    PosterImage,
    PosterContainer,
    PosterOverlay,
    PosterTitle,
} from './Poster.styles'
import NominationModal from '../NominationModal/NominationModal'
import NomModalTest from '../NominationModal/NomModalTest'
import Modal from '@material-ui/core/Modal';

const Poster = ( props ) => {
    const [open, setOpen] = useState(false);

	const handleOpen = () => {
		setOpen(true);
	  };
	
	const handleClose = () => {
		setOpen(false);
	};
	
	return (
        <PosterContainer>
            <PosterImage 
                src={props.movie.Poster} 
                alt='movie'
                onClick = {() => handleOpen()}
            />
            <NomModalTest
                handleNominationClick={props.handleNominationClick}
                open={open}
                onClose={handleClose}
                movie={props.movie}
            />
        </PosterContainer>
	);
};

export default Poster;