import React, { useState, useEffect } from 'react';
import Modal from '@material-ui/core/Modal';
import { 
	StyledModal,
	MovieTitle,
	MovieYear,
	NominateBtn,
}
from './NominationModal.styles';
import Button from "../../GlobalStyles"
import { black } from 'color-name';
import { flexbox } from '@material-ui/system';

function NominationModal ( props ) {
	const [movieInfo, setMovieInfo] = useState([]);
	function rand() {
		return Math.round(Math.random() * 20) - 10;
	}	

	function getModalStyle() {
		const top = 50 + rand();
		const left = 50 + rand();
	  
		return {
		  top: `${top}%`,
		  left: `${left}%`,
		  transform: `translate(-${top}%, -${left}%)`,
		};
	  }
	  
	  const styles = ((theme) => ({
		paper: {
		  position: 'absolute',
		  width: 400,
		  backgroundColor: black,
		  border: '2px solid #000',
		},
	}));

	const getMovieInfoRequest = async () => {
		const url = `https://www.omdbapi.com/?apikey=d8daf75a&i=${
			props.movie.imdbID
		}&plot=full`;

		const response = await fetch(url);
		const responseJson = await response.json();

		if (responseJson.data) {
			setMovieInfo(responseJson.data);
		}
	};

	useEffect(() => {
		getMovieInfoRequest();
	});

	const body = (
		<div>
			<MovieTitle>{props.movie.Title}</MovieTitle>
			<MovieYear>{props.movie.Year}</MovieYear>		
		</div>
	  );

	return ( 
		<div>
			<Button>Test Button</Button>
			<Modal 
			open={props.open} 
			onClose={props.onClose}
			>
				<div className="modal-contents">
					
					
				</div>
			</Modal>
		</div>
	);
};

export default NominationModal; 